* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 10px;
}

#map {
  border: 20px solid black;
  width: 868px;
  height: 1200px;
}

.inputs-wrapper {
  .input, .dropdown {
    margin-bottom: 15px;
  }
}

.editor-wrapper {
  position: relative;

  .text {
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    padding: 15px 30px;
    background-color: white;

    &__headline {
      color: #000;
      text-align: center;
      font-size: 24px;
      font-weight: bold;
    }

    &__secondary {
      color: #000;
      text-align: center;
      margin-bottom: 5px;
    }
  }
}
